*{
  text-align: center;
  margin: 30px auto;
}

.heading{
  font-size: 3rem;
  color: white ;
  margin: 0 auto;
  width: 400px;
  padding: 5px 5px;
  border: 2px solid #93B5C6;
  border-radius: 10px;
  background-color: #0d0813;
}
.heading:hover{
  background-color: white;
  color: blueviolet;
}

.text{
  width:250px;
  height: 30px;
  margin-right: 5px;
  border: 2px solid #125D98;
  border-radius: 10px;
  font-size: 18px;
  text-align: left;
}
.button{
  width: 55px;
  height: 30px;
  border-radius: 8px;
}
.button:hover{
  background-color: #9D84B7;
  color: white;
}

.center{
  margin: 2px auto;
  text-align: center;
  font-size: 1.9rem;
}

.update-list{
  margin-top: 5px;
  margin-left: 43%;
  margin-right: 43%;
  margin-bottom: 0px;
  border: 2px solid grey;
  background-color: grey;
  
}
